import { onMounted, onBeforeUnmount } from "vue";
import { isArray, len } from "../format/arrary";
const useMounted = (fns) => {
  if (!isArray(fns)) {
    console.log("[Fect] useMounted must entry array");
    return;
  }
  const filterFunc = (fns2) => {
    const single = len(fns2) === 1;
    if (single)
      return {
        start: fns2[0],
        end: fns2[0]
      };
    const [start2, ...ends] = fns2;
    return {
      start: start2,
      end: ends
    };
  };
  const { start, end } = filterFunc(fns);
  onMounted(start);
  onBeforeUnmount(() => {
    if (isArray(end)) {
      end.forEach((_) => _.apply(null));
      return;
    }
    end();
    return;
  });
};
export {
  useMounted
};
