const omit = (source, picks) => Object.keys(source).reduce((acc, cur) => picks.includes(cur) ? acc : Object.assign(acc, { [cur]: source[cur] }), {});
const assign = (source, ...rest) => Object.assign(source, ...rest);
const hasOwn = (source, key) => Object.hasOwnProperty.call(source, key);
const pick = (source, picks) => picks.reduce((acc, cur) => (acc[cur] = source[cur], acc), {});
const isPlainObject = (tar) => typeof tar === "object" && Object.prototype.toString.call(tar) === "[object Object]";
export {
  assign,
  hasOwn,
  isPlainObject,
  omit,
  pick
};
