import { createProvider, useProvider } from "@fect-ui/vue-hooks";
const READONLY_MODAL_KEY = Symbol("modalKey");
const createModalContext = () => createProvider(READONLY_MODAL_KEY);
const useModalContext = () => useProvider(READONLY_MODAL_KEY);
export {
  READONLY_MODAL_KEY,
  createModalContext,
  useModalContext
};
