import { createProvider, useProvider } from "@fect-ui/vue-hooks";
const READONLY_CHECKBOX_KEY = Symbol("checkboxKey");
const createCheckboxContext = () => createProvider(READONLY_CHECKBOX_KEY);
const useCheckboxContext = () => useProvider(READONLY_CHECKBOX_KEY);
export {
  READONLY_CHECKBOX_KEY,
  createCheckboxContext,
  useCheckboxContext
};
