import { createApp, unref } from "vue";
const createNode = (name, node = "div") => {
  const dom = document.querySelector(`.${name}`);
  if (dom)
    return dom;
  const elSnapshot = document.createElement(node);
  elSnapshot.classList.add(name);
  document.body.appendChild(elSnapshot);
  return elSnapshot;
};
const createPortal = (children, container) => {
  const elSnapshot = unref(container) || document.createElement("div");
  const app = createApp(children);
  document.body.appendChild(elSnapshot);
  const instance = app.mount(elSnapshot);
  return {
    instance
  };
};
export {
  createNode,
  createPortal
};
