import { createProvider, useProvider } from "@fect-ui/vue-hooks";
const READONLY_SKELETON_KEY = Symbol("skeletonKey");
const createSkeletonContext = () => createProvider(READONLY_SKELETON_KEY);
const useSkeletonContext = () => useProvider(READONLY_SKELETON_KEY);
export {
  createSkeletonContext,
  useSkeletonContext
};
