import { getCurrentInstance } from "vue";
const routeProps = {
  to: [String, Object]
};
const route = (vm) => {
  const router = vm.$router;
  const { to } = vm;
  if (to && router) {
    router["push"](to);
  }
};
const useRoute = () => {
  const vm = getCurrentInstance().proxy;
  return () => route(vm);
};
export {
  route,
  routeProps,
  useRoute
};
