import { createProvider, useProvider } from "@fect-ui/vue-hooks";
const READONLY_TOAST_KEY = Symbol("toast");
const createToastContext = () => createProvider(READONLY_TOAST_KEY);
const useToastContext = () => useProvider(READONLY_TOAST_KEY);
export {
  READONLY_TOAST_KEY,
  createToastContext,
  useToastContext
};
