import { addUnit } from "../utils";
const labelPosition = ["left", "right", "top"];
const getLabelPostion = (position) => {
  if (!position)
    return;
  if (labelPosition.includes(position) && position !== "top")
    return position;
  return;
};
const getLabelWidth = (width) => addUnit(width);
export {
  getLabelPostion,
  getLabelWidth
};
