import { createProvider, useProvider } from "@fect-ui/vue-hooks";
const READONLY_TABS_KEY = Symbol("tabsKey");
const createTabsContext = () => createProvider(READONLY_TABS_KEY);
const useTabsContext = () => useProvider(READONLY_TABS_KEY);
export {
  READONLY_TABS_KEY,
  createTabsContext,
  useTabsContext
};
