import { createProvider, useProvider } from "@fect-ui/vue-hooks";
const READONLY_PAGINATION_KEY = Symbol("paginationKey");
const createPaginationContext = () => createProvider(READONLY_PAGINATION_KEY);
const usePaginationContext = () => useProvider(READONLY_PAGINATION_KEY);
export {
  createPaginationContext,
  usePaginationContext
};
