import { createProvider, useProvider } from "@fect-ui/vue-hooks";
const READONLY_AVATAR_KEY = Symbol("avatarKey");
const createAvatarContext = () => createProvider(READONLY_AVATAR_KEY);
const useAvatarContext = () => useProvider(READONLY_AVATAR_KEY);
export {
  READONLY_AVATAR_KEY,
  createAvatarContext,
  useAvatarContext
};
