const CAMELIZERE = /-(\w)/g;
const KEBACASE = /[A-Z]+(?![a-z])|[A-Z]/g;
const camelize = (str) => str.replace(CAMELIZERE, (_, key) => key.toUpperCase());
const hasEmpty = (str) => {
  if (typeof str === "undefined")
    return true;
  if (isNaN(str))
    return true;
  if (str === null)
    return true;
  return false;
};
const getId = () => Math.random().toString(32).slice(2, 10);
const kebabCase = (str) => str.replace(KEBACASE, (_, ofs) => (ofs ? "-" : "") + _.toLowerCase());
export {
  camelize,
  getId,
  hasEmpty,
  kebabCase
};
