import { createProvider, useProvider } from "@fect-ui/vue-hooks";
const READONLY_COLLAPSE_KEY = Symbol("collapseKey");
const createCollapseContext = () => createProvider(READONLY_COLLAPSE_KEY);
const useCollapseContext = () => useProvider(READONLY_COLLAPSE_KEY);
export {
  READONLY_COLLAPSE_KEY,
  createCollapseContext,
  useCollapseContext
};
