import { watch, onMounted } from "vue";
import { useState } from "@fect-ui/vue-hooks";
const useTheme = () => {
  const [theme, setTheme] = useState();
  const themeChange = () => {
    if (typeof window === "undefined" || !window.localStorage)
      return;
    setTheme((pre) => pre === "light-theme" ? "dark-theme" : "light-theme");
  };
  onMounted(() => {
    if (typeof window === "undefined" || !window.localStorage)
      return;
    const isDarkMode = matchMedia("(prefers-color-scheme)").media !== "not all" && matchMedia("(prefers-color-scheme: dark)").matches;
    const theme2 = localStorage.getItem("theme") || (isDarkMode ? "dark-theme" : "light-theme");
    setTheme(theme2);
  });
  watch(theme, (pre) => {
    if (typeof window === "undefined" || !window.localStorage)
      return;
    localStorage.setItem("theme", pre);
    const root = document.querySelector("html");
    root.setAttribute("class", theme.value);
  });
  return {
    themeChange,
    theme
  };
};
export {
  useTheme
};
